function getUserInfo(successCallback,errorCallback) {
    uni.showLoading({
        title: '',
    });

    uni.getUserProfile({
        desc: '',
        success: function(res) {
            uni.hideLoading()
            var offUserInfo = res.userInfo
            successCallback && successCallback(offUserInfo)
        },fail: (res) => {
            uni.hideLoading()
            errorCallback && errorCallback(res)
        }
    })















}

export {
    getPhoneInfo,
    getUserInfo
}