import {
	BaseApi
} from './baseApi.js'
export default {
	config: {
		baseUrl: BaseApi,
		header: {
			'Content-Type':'application/json;charset=UTF-8',
			// 'Content-Type':'application/x-www-form-urlencoded'
		},  
		data: {},
		method: "GET",
		dataType: "json",  /* json JSON.parse */
		responseType: "text",
		success() {},
		fail() {},
		complete() {}
	},
	interceptor: {
		request: null,
		response: null
	},
	request(options) {
		if (!options) {
			options = {}
		}
		options.baseUrl = options.baseUrl || this.config.baseUrl
		options.dataType = options.dataType || this.config.dataType
		options.url = options.baseUrl + options.url
		options.data = options.data || {}
		options.method = options.method || this.config.method
		
		return new Promise((resolve, reject) => {
			let _config = null
			
			options.complete = (response) => {
				let statusCode = response.statusCode
				response.config = _config
				if (process.env.NODE_ENV === 'development') {
					if (statusCode === 200) {
						////console.log("" + _config.requestId + " " + JSON.stringify(response.data))
					}
				}
				if (this.interceptor.response) {
					
					let newResponse = this.interceptor.response(response)
					if (newResponse) {
						response = newResponse
					}
				}
				// 
				//_reslog(response)
				if (statusCode === 200) { //
					resolve(response);
				} else {
					reject(response)
				}
			}

			_config = Object.assign({}, this.config, options)
			_config.requestId = new Date().getTime()

			if (this.interceptor.request) {
				this.interceptor.request(_config)
			}
			
			// 
			//_reqlog(_config)

			if (process.env.NODE_ENV === 'development') {
				console.log("" + _config.requestId + " " + _config.url)
				if (_config.data) {
					console.log("" + _config.requestId + " " + JSON.stringify(_config.data))
				}
			}

			uni.request(_config);
		});
	}	
}


/**
 * 
 */
function _reqlog(req) {
	if (process.env.NODE_ENV === 'development') {
		console.log("" + req.requestId + " " + req.url)
		if (req.data) {
			console.log("" + req.requestId + " " + JSON.stringify(req.data))
		}
	}
	//TODO 
}

/**
 * 
 */
function _reslog(res) {
	let _statusCode = res.statusCode;
	if (process.env.NODE_ENV === 'development') {
		console.log("" + res.config.requestId + " " + res.config.url)
		if (res.config.data) {
			console.log("" + res.config.requestId + " " + JSON.stringify(res.config.data))
		}
		console.log("" + res.config.requestId + " " + JSON.stringify(res))
	}
	//TODO 
	switch(_statusCode){
		case 200:
			break;
		case 401:
			break;
		case 404:
			break;
		default:
			break;
	}
}

